		// },
		//控制左滑删除效果-begin
		touchStart(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
		},
		touchMove(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			if(this.isStop||Math.abs(moveX)<5){
				return ;
			}
			if (Math.abs(moveY) > Math.abs(moveX)){
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			
			if(moveX<0){
				this.theIndex = index;
				this.isStop = true;
			}else if(moveX>0){
				if(this.theIndex!=null&&this.oldIndex==this.theIndex){
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(()=>{
						this.oldIndex = null;
					},150)
				}
			}
		},
		touchEnd(index,$event){
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		
		
		//商品跳转
		linkToDetail(e){
			uni.showToast({title: '商品'+e.id,icon:"none"});
			uni.navigateTo({
				url: '/pages/productDetail/productDetail'
			});
		},
		//跳转确认订单页面
		linkToPayment(){
			let tmpList=[];
			let len = this.goodsList.length;
			for(let i=0;i<len;i++){
				if(this.goodsList[i].selected) {
					tmpList.push(this.goodsList[i]);
				}
			}
			if(tmpList.length<1){
				uni.showToast({
					title:'请选择商品结算',
					icon:'none'
				});
				return ;
			}
			uni.setStorage({
				key:'buylist',
				data:tmpList,
				success: () => {
					uni.navigateTo({
						url:'/pages/payment/payment'
					})
				}
			})
		},
		//删除商品
		deleteGoods(id){
			uni.showModal({
			    title: '提示',
			    content: '确定要删除选中商品吗？',
				confirmColor: '#E93548',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			let len = this.goodsList.length;
			for(let i=0;i<len;i++){
				if(id==this.goodsList[i].id){
					this.goodsList.splice(i, 1);
					break;
				}
			}
			this.selectedList.splice(this.selectedList.indexOf(id), 1);
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		// 删除商品s
		deleteList(){
			let len = this.selectedList.length;
			while (this.selectedList.length>0)
			{
				this.deleteGoods(this.selectedList[0]);
			}
			this.selectedList = [];
			this.isAllselected = this.selectedList.length == this.goodsList.length && this.goodsList.length>0;
			this.sum();
		},
		// 选中商品
		selected(index){
			this.goodsList[index].selected = this.goodsList[index].selected?false:true;
			let i = this.selectedList.indexOf(this.goodsList[index].id);
			i>-1?this.selectedList.splice(i, 1):this.selectedList.push(this.goodsList[index].id);
			this.isAllselected = this.selectedList.length == this.goodsList.length;
			this.sum();
		},
		//全选
		allSelect(){
			let len = this.goodsList.length;
			let arr = [];
			for(let i=0;i<len;i++){
				this.goodsList[i].selected = this.isAllselected? false : true;
				arr.push(this.goodsList[i].id);
			}
			this.selectedList = this.isAllselected?[]:arr;
			this.isAllselected = this.isAllselected||this.goodsList.length==0?false : true;
			this.sum();
		},
		// 减少数量
		sub(index){
			if(this.goodsList[index].number<=1){
				return;
			}
			this.goodsList[index].number--;
			this.sum();
		},
		// 增加数量
		add(index){
			this.goodsList[index].number++;
			this.sum();
		},
		// 合计
		sum(e,index){
			this.sumPrice=0;
			let len = this.goodsList.length;
			for(let i=0;i<len;i++){
				if(this.goodsList[i].selected) {
					if(e && i==index){
						this.sumPrice = this.sumPrice + (e.detail.value*this.goodsList[i].price);
					}else{
						this.sumPrice = this.sumPrice + (this.goodsList[i].number*this.goodsList[i].price);
					}
				}
			}
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		discard() {
			//丢弃
		}
		
		
	}
